////
/// Variables.
////

////
/// Custom header.
////

.wp-custom-header {
	display: flex;
	align-items: center;
	justify-content: center;

	@include overlay;
	@include position(absolute, 0 0 0 0);

	// Video play/pause button.
	&-video-button {
		display: none;
	}

	// Video play button.
	&-video-play {
	}

	// Video pause button.
	&-video-pause {
	}

	// Image.
	img {
		max-width: none;
	}
}
